.header {
  padding: 0 50px;
  display: flex;
  background: #fff;
  justify-content: space-between;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
  --color-link: #00acdc;

  &__item {
    list-style-type: none;
    display: inline;
  }

  &__logo {
    padding-top: 10px;
  }

  &__link {
    text-decoration: none;
    color: var(--main-accent);
    display: inline-block;
    position: relative;
    line-height: 14px;
    margin-left: 17px;
    padding: 23px 0;

    &:hover {
      color: #00acdc;
    }

    &:focus::after {
      color: var(--color-link);
      position: absolute;
      content: "";
      height: 4px;
      bottom: 0;
      border-radius: 8px;
      margin: 0 auto;
      left: 0;
      right: 0;
      width: 100%;
      background: var(--color-link);
    }
  }
}

.is-active {
  color: var(--color-link);
  margin: 0;

  &::after {
    margin: 0;
    color: var(--color-link);
    position: absolute;
    content: "";
    height: 4px;
    bottom: 0;
    border-radius: 8px;
    left: 0;
    right: 0;
    width: 100%;
    background: var(--color-link);
  }
}
